import Header from './header';
import styled, { ThemeProvider } from 'styled-components';

const theme = {
  shadowColor: 'rgb(250, 250, 250)',
  fontColor: 'rgb(50,50,50,)'
};

const Layout = props => (
  <ThemeProvider theme={theme}>
    <>
      <Header />
      {props.children}
    </>
  </ThemeProvider>
);

export default Layout;
